.button {
  align-items: center;
  appearance: none;
  background-image: radial-gradient(100% 100% at 100% 0, #5adaff 0, #5468ff 100%);
  border: 0;
  border-radius: 6px;
  box-shadow: rgba(45, 35, 66, 0.4) 0 2px 4px, rgba(45, 35, 66, 0.3) 0 7px 13px -3px, rgba(58, 65, 111, 0.5) 0 -3px 0 inset;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: inline-flex;
  font-family: "JetBrains Mono", monospace;
  height: 48px;
  justify-content: center;
  line-height: 1;
  list-style: none;
  overflow: hidden;
  outline: none;
  padding-left: 16px;
  padding-right: 16px;
  position: relative;
  text-align: left;
  text-decoration: none;
  transition: box-shadow 0.15s, transform 0.15s;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
  will-change: box-shadow, transform;
  font-size: 18px;
}

.button:hover {
  box-shadow: rgba(45, 35, 66, 0.4) 0 4px 8px, rgba(45, 35, 66, 0.3) 0 7px 13px -3px, #3c4fe0 0 -3px 0 inset;
}

.button:active {
  box-shadow: #3c4fe0 0 3px 7px inset;
  transform: translateY(2px);
}

.inactive {
  box-shadow: rgba(41, 41, 41, 0.4) 0 2px 4px, rgba(41, 41, 41, 0.3) 0 7px 13px -3px, rgba(60, 60, 60, 0.5) 0 -3px 0 inset;
  background-image: radial-gradient(100% 100% at 100% 0, #3d3b3c 0, #737373 100%);
  cursor: not-allowed;
}

.inactive:hover {
  box-shadow: rgba(41, 41, 41, 0.4) 0 2px 4px, rgba(41, 41, 41, 0.3) 0 7px 13px -3px, rgba(60, 60, 60, 0.5) 0 -3px 0 inset;
  transform: none;
}

.inactive:active {
  box-shadow: rgba(41, 41, 41, 0.4) 0 2px 4px, rgba(41, 41, 41, 0.3) 0 7px 13px -3px, rgba(60, 60, 60, 0.5) 0 -3px 0 inset;
  transform: none;
}

.red {
  background-image: radial-gradient(100% 100% at 100% 0, #ff5aaf 0, #ff5454 100%);
}

.red:hover {
  box-shadow: rgba(45, 35, 66, 0.4) 0 4px 8px, rgba(45, 35, 66, 0.3) 0 7px 13px -3px, #9e4d64 0 -3px 0 inset;
}

.red:active {
  box-shadow: #6a2626 0 3px 7px inset;
}

.error {
  background-image: radial-gradient(100% 100% at 100% 0, #ff5aaf 0, #ff5454 100%);
  cursor: not-allowed;
}

.error:hover {
  box-shadow: rgba(45, 35, 66, 0.4) 0 2px 4px, rgba(45, 35, 66, 0.3) 0 7px 13px -3px, rgba(58, 65, 111, 0.5) 0 -3px 0 inset;
  transform: none;
}

.error:active {
  box-shadow: rgba(45, 35, 66, 0.4) 0 2px 4px, rgba(45, 35, 66, 0.3) 0 7px 13px -3px, rgba(58, 65, 111, 0.5) 0 -3px 0 inset;
  transform: none;
}

.loading {
  background-image: radial-gradient(100% 100% at 100% 0, #3d3b3c 0, #737373 100%);
  cursor: progress;
}

.loading:active {
  box-shadow: rgba(45, 35, 66, 0.4) 0 2px 4px, rgba(45, 35, 66, 0.3) 0 7px 13px -3px, rgba(58, 65, 111, 0.5) 0 -3px 0 inset;
  transform: none;
}

.loading:hover {
  box-shadow: rgba(45, 35, 66, 0.4) 0 2px 4px, rgba(45, 35, 66, 0.3) 0 7px 13px -3px, rgba(58, 65, 111, 0.5) 0 -3px 0 inset;
  transform: none;
}
